/*Header*/
.header {
  margin-bottom: 90px;
  @media (max-width: 1199.9px) {
    margin-bottom: 60px;
  }
  @media (max-width: 575.9px) {
    margin-bottom: 40px;
  }
  &__contacts {
    background-color: $contacts_bg;
    @media (max-width: 767.9px) {
      display: none;
    }
  }

  &__body {
    min-height: 450px;
    position: relative;
    @media (max-width: 1199.9px) {
      min-height: 500px;
    }
    @media (max-width: 480px) {
      min-height: 490px;
    }
  }

  &__body-img {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    height: 100%;
    width: 100%;
    object-fit: cover;
  }

  &__contacts-container {
    padding: 0 2px;
  }

  &__contacts-subscription {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__contacts-mail {
    display: flex;
    align-items: center;
  }

  &__navbar {
    position: relative;
    z-index: 3;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 66px;
    padding: 2px;
  }

  &__navbar-label {
    @media (max-width: 1199.9px) {
      margin-left: 25px;
    }
    @media (max-width: 480px) {
      margin-left: 10px;
    }
    &:focus {
      outline: 1px solid transparent;
    }
  }

  &__navbar-img {
    width: 148px;
    height: 44px;
    @media (max-width: 1199.9px) {
      width: 100px;
      height: 30px;
    }
  }

  &__content {
    position: relative;
    z-index: 2;
    margin-top: 60px;
  }
  &__content-body {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__content-tilte {
    margin-bottom: 53px;
  }

  &__content-data {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__content-input {
    width: 66.8%;
    height: 56px;
    border-radius: 10px;
    position: relative;
    z-index: 4;
    padding: 18px 16px;
    &:focus {
      outline: 1px solid transparent;
    }
    &::placeholder {
      font-family: 'r';
      font-size: 14px;
      line-height: 120%;
      @media (max-width: 480px) {
        font-size: 12px;
      }
    }
    @media (max-width: 767.9px) {
      width: 80%;
    }

    @media (max-width: 480px) {
      width: 91.7%;
      font-size: 12px;
    }
  }

  &__content-btn {
    font-size: 16px;
    line-height: 110%;
    font-family: 'b';
    color: $white;
    background-color: $contacts_bg;
    padding: 14px 22px 14px 42px;
    border-radius: 10px;
    margin-left: -120px;
    position: relative;
    z-index: 5;
    &:focus {
      outline: 1px solid transparent;
    }
    @media (max-width: 480px) {
      display: none;
    }

    &::before {
      content: '';
      background: url('../img/icons/search.svg') no-repeat;
      position: absolute;
      top: 16px;
      left: 21px;
      width: 15px;
      height: 15px;
      object-fit: cover;
    }
  }
  &__content-btn-sm {
    background-color: $contacts_bg;
    padding: 14px 22px 14px 22px;
    border-radius: 10px;
    display: none;
    margin-left: -64px;
    position: relative;
    z-index: 5;
    &:focus {
      outline: 1px solid transparent;
    }
    @media (max-width: 480px) {
      display: block;
    }
  }
  &__content-img-sm {
    width: 15px;
    height: 15px;
  }

  &__content-buttons {
    width: 100%;
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 12px;
  }

  &__content-button {
    padding: 12px 10px 12px 28px;
    background-color: $light-white;
    border-radius: 10px;
    color: $white;
    &:focus {
      outline: 1px solid transparent;
    }
    @media (max-width: 480px) {
      padding: 8px 11px 8px 27px;
    }
    &:last-child {
      @media (max-width: 480px) {
        display: none;
      }
    }
  }
}
.socials {
  &__content {
    display: flex;
    align-items: center;
    height: 60px;
  }

  &__title {
    font-size: 18px;
    font-family: 'r';
    line-height: 120%;
    color: $white;
  }

  &__items {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 3px;
  }
  &__link {
    &:first-child {
      margin-left: 15px;
    }
    margin-left: -25px;
    &:active {
      background: transparent;
    }
    &:focus {
      outline: 1px solid transparent;
    }
  }
}
.connection {
  &__mail {
    display: block;
    margin-right: 20px;
    font-size: 18px;
    font-family: 'r';
    line-height: 120%;
    color: $white;
    position: relative;
    &:active {
      background: transparent;
    }
    &:focus {
      outline: 1px solid transparent;
    }
    &::before {
      content: '';
      background: url('../img/icons/mail.svg') no-repeat;
      position: absolute;
      top: 5px;
      left: -30px;
      width: 32px;
      height: 32px;
      object-fit: cover;
    }
  }

  &__btn {
    font-size: 18px;
    font-family: 'r';
    line-height: 120%;
    color: $white;
    background: transparent;
    &:focus {
      outline: 1px solid transparent;
    }
  }
}
.nav {
  &__list {
    margin-right: -58px;
    @media (max-width: 1400px) {
      margin-right: 0px;
    }
    @media (max-width: 1199.9px) {
      order: -1;
      margin-left: 37.5px;
    }
    @media (max-width: 480px) {
      margin-left: 20px;
    }
  }
  &__inner {
    display: flex;
    align-items: center;
    @media (max-width: 1199.9px) {
      margin-right: 20px;
    }
    @media (max-width: 480px) {
      margin-right: 10px;
    }
    &.access .nav__btn {
      display: none;
    }
    &.access .dropmenu {
      display: block;
    }
  }

  &__btn {
    display: block;
    padding: 14px 20px;
    background-color: $contacts_bg;
    border-radius: 10px;
    font-size: 16px;
    line-height: 110%;
    font-family: $b;
    color: $white;
    margin-right: 12px;
    &:focus {
      outline: 1px solid transparent;
    }

    @media (max-width: 1199.9px) {
      margin-right: 5px;
      padding: 9px 20px;
    }
    @media (max-width: 480px) {
      margin-right: 0px;
    }
  }

  &__select {
    position: relative;
    border: 1px solid #ccc;
    border-radius: 10px;
    background: transparent;
    width: 125px;
    &.is-active .select__body {
      display: flex;
      align-items: center;
      flex-direction: column;
      padding: 2px;
    }
  }

  &__registration {
    margin-right: 12px;
    margin-top: 4px;
    display: flex;
    justify-content: flex-end;
  }
}
.dropmenu {
  display: none;
  &__image {
    position: relative;
  }

  &__img {
    width: 46px;
    height: 46px;
    object-fit: cover;
  }

  &__body {
    width: 332px;
    background-color: $white;
    padding: 36px 10px 40px 20px;
    border-radius: 10px;
    min-height: 1px;
    position: absolute;
    top: 100%;
    z-index: 4;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
    &.open {
      opacity: 1;
      visibility: visible;
    }
  }

  &__body-header {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
  }

  &__body-avatar {
    margin-right: 20px;
  }

  &__body-text {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__body-title {
    font-family: 'b';
    font-size: 16px;
    line-height: 110%;
    color: $menu-black;
  }

  &__body-desc {
    color: $email-gray;
    letter-spacing: 0%;
    font-size: 16px;
    font-family: 'ir';
  }

  &__body-list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__body-li {
    margin-bottom: 14px;
    &:nth-child(2) {
      margin-bottom: 30px;
    }
    &:last-child {
      margin-bottom: 0px;
    }
  }
  &__body-link {
    font-family: 'ir';
    font-size: 16px;
    letter-spacing: 0%;
    color: $menu-black;
    display: flex;
    align-items: center;
  }

  &__body-icon {
    margin-right: 5px;
  }

  &__body-info {
  }
  hr {
    width: 96%;
    background-color: $light-gray;
    height: 1px;
    margin-bottom: 30px;
  }
}

.select {
  &__header {
    cursor: pointer;
    display: flex;
    position: relative;
    z-index: 4;
  }

  &__current {
    display: block;
    padding: 13px 0px 13px 13px;
    width: 100%;
    font-size: 14px;
    font-family: 'b';
    color: white;
    display: flex;
    align-items: center;
    @media (max-width: 1199.9px) {
      padding: 9px 0px 9px 13px;
    }
  }

  &__body {
    border-top: 0;
    display: none;
    left: 0;
    position: absolute;
    right: 0;
    top: 110%;
    border: 1px solid #ccc;
    border-radius: 10px;
    z-index: 4;
    background-color: $white;
  }

  &__item {
    cursor: pointer;
    font-size: 14px;
    font-family: $s;
    color: $contacts_bg;
    padding: 10px;
    position: relative;

    &:after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      border-bottom: 1px solid #d9d9d9;
      z-index: 4;
      width: 86px;
      opacity: 0;
      visibility: hidden;
    }
    @media (hover: hover) {
      &:hover {
        &::after {
          opacity: 1;
          visibility: visible;
        }
        color: $light-gray;
      }
    }
  }
}
.select-arrow {
  transition: transform 0.2s ease;
  display: inline-block;
  transform-origin: center;
  position: absolute;
  top: 42%;
  right: 10px;
  &.is-active {
    transform: rotateX(180deg);
  }
}

.menu {
  &__icon {
    opacity: 1;
    visibility: visible;
    &:focus {
      outline: 1px solid transparent;
    }
  }

  &__body-close {
    display: none;
  }

  &__list {
    @media (max-width: 1199.9px) {
      flex-wrap: wrap;
      justify-content: center;
    }
    @media (max-width: 575.9px) {
      height: auto;
    }
  }

  &__list-li {
    margin-right: 30px;
    &:last-child {
      margin-right: 0;
    }
    @media (max-width: 1399.9px) {
      margin-right: 10px;
    }
  }

  &__list-link {
    font-size: 20px;
    line-height: 120%;
    font-family: 'b';
    padding: 11px 20px;
    color: $white;
    border: 1px solid $white;
    border-radius: 10px;

    @media (max-width: 1399.9px) {
      font-size: 18px;
    }
    &:active {
      background: transparent;
    }
    &:focus {
      outline: 1px solid transparent;
    }
  }
  &__footer {
    margin-top: 100px;
    display: none;
    @media (max-width: 1199.9px) {
      display: block;
    }
  }

  &__footer-title {
    font-family: 'b';
    font-size: 18px;
    line-height: 120%;
    color: $menu-black;
    margin-bottom: 15px;
  }

  &__footer-container {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    column-gap: 20px;
  }
}
.title-header {
  text-align: center;
  &__heading {
    font-size: 44px;
    line-height: 110%;
    font-family: 'b';
    color: $white;
    @media (max-width: 480px) {
      font-size: 30px;
    }
    @media (max-width: 320px) {
      font-size: 25px;
    }
  }

  &__desc {
    font-size: 18px;
    line-height: 120%;
    font-family: 'r';
    color: $white;
    @media (max-width: 480px) {
      font-size: 18px;
    }
    @media (max-width: 320px) {
      font-size: 16px;
    }
  }
  &__heading-styles {
    margin-bottom: 8px;
  }
}
.glass {
  position: relative;
  &::before {
    content: '';
    background: url('../img/icons/search.svg') no-repeat;
    position: absolute;
    top: 14px;
    left: 10px;
    width: 15px;
    height: 15px;
    object-fit: cover;
  }
  @media (max-width: 480px) {
    &::before {
      top: 10px;
    }
  }
}

.menu__icon {
  display: none;
}

@media (min-width: 1199.9px) {
  .menu__list {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
  }

  .menu__list > li {
    padding: 10px 0px;
  }
}

@media (max-width: 1199.9px) {
  .menu__icon {
    z-index: 5;
    display: block;
    position: relative;
    width: 30px;
    height: 18px;
    cursor: pointer;
    opacity: 1;
    visibility: visible;
  }

  .menu__icon span,
  .menu__icon::before,
  .menu__icon::after {
    left: 0;
    position: absolute;
    height: 10%;
    width: 100%;
    -webkit-transition: all 0.3s ease 0s;
    -o-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
    background-color: #fff;
    opacity: 1;
    visibility: visible;
  }

  .menu__icon::before,
  .menu__icon::after {
    content: '';
  }

  .menu__icon::before {
    top: 0;
  }

  .menu__icon::after {
    bottom: 0;
  }
  .menu__icon span {
    top: 50%;
    -webkit-transform: scale(1) translate(0px, -50%);
    -ms-transform: scale(1) translate(0px, -50%);
    transform: scale(1) translate(0px, -50%);
  }
  .menu__icon._active,
  .menu__icon._active span,
  .menu__icon._active::before,
  .menu__icon._active::after {
    opacity: 0;
    visibility: hidden;
  }

  .menu__body-close {
    display: block;
    position: absolute;
    top: 30px;
    right: 30px;
  }

  .menu__body {
    position: fixed;
    top: 0;
    left: -100%;
    width: 40%;
    height: 100%;
    background-color: $white;
    padding: 86px 30px 30px 16px;
    -webkit-transition: left 0.3s ease 0s;
    -o-transition: left 0.3s ease 0s;
    transition: left 0.3s ease 0s;
    overflow: auto;
    z-index: 6;
  }

  .menu__body._active {
    left: 0;
    height: 650px;
    @media (max-width: 767.9px) {
      width: 50%;
    }
    @media (max-width: 575.9px) {
      width: 70%;
    }
    @media (max-width: 480px) {
      width: 100%;
    }
  }

  .menu__list > li {
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin: 0px 0px 36px 0px;
  }

  .menu__list > li:last-child {
    margin-bottom: 0;
  }

  .menu__list-link {
    padding: 0;
    border: none;
    color: $menu-black;
    @media (hover: hover) {
      &:hover {
        color: $contacts_bg;
      }
    }
  }
  .select__current {
    padding: 13px 0px 13px 13px;
    color: $contacts_bg;
  }
  .select__body {
    border: 1px solid $contacts_bg;
  }
  .nav__select {
    position: relative;
    border: 1px solid $contacts_bg;
    border-radius: 10px;
    background: transparent;
    width: 125px;
  }
  .select__item:first-child {
    display: none;
  }
}
/*Header-End*/
