.categories {
  &__container {
    width: 100%;
  }

  &__header {
    display: flex;
    align-items: flex-end;
    margin-bottom: 50px;
    justify-content: space-between;
    padding: 0 5px;
    @media (max-width: 1199.9px) {
      padding: 0 30px;
    }
    @media (max-width: 767.9px) {
      flex-direction: column;
      align-items: center;
      padding: 0 25px;
    }
  }

  &__header-content {
    width: 60%;
    @media (max-width: 1199.9px) {
      width: 80%;
    }
    @media (max-width: 991.9px) {
      width: 70%;
    }
    @media (max-width: 767.9px) {
      width: 100%;
      display: flex;
      align-items: center;
      flex-direction: column;
      text-align: center;
    }
  }

  &__header-title {
    font-family: 'ib';
    font-size: 52px;
    line-height: 110%;
    color: $menu-black;
    margin-bottom: 14px;
    @media (max-width: 575.9px) {
      font-size: 30px;
    }
  }

  &__header-text {
    font-family: 'b';
    font-size: 18px;
    line-height: 120%;
    color: $menu-black;
    @media (max-width: 767.9px) {
      margin-bottom: 20px;
    }
    @media (max-width: 575.9px) {
      font-size: 14px;
    }
  }

  &__header-button {
    padding: 14px 20px;
    background-color: $contacts_bg;
    color: $white;
    width: 163px;
    border-radius: 10px;

    font-family: 'b';
    font-size: 16px;
    line-height: 110%;

    @media (max-width: 575.9px) {
      width: 186px;
      padding: 14px 30px;
      font-size: 14px;
    }
  }

  &__grid {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
    @media (max-width: 1199.9px) {
      justify-content: center;
      row-gap: 36px;
      column-gap: 40px;
    }

    padding: 0 5px;
    @media (max-width: 1199.9px) {
      padding: 0 30px;
    }
    @media (max-width: 767.9px) {
      padding: 0 16px;
    }
    @media (max-width: 320px) {
      padding: 0 5px;
    }
  }

  &__card {
    width: 332px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    box-sizing: border-box;
    @media (max-width: 1199.9px) {
      &:nth-child(4) {
        display: none;
      }
    }
    @media (max-width: 767.9px) {
      width: 80%;
    }
    @media (max-width: 575.9px) {
      width: 100%;
    }
  }

  &__card-image {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    overflow: hidden;
    position: relative;
    padding: 0% 0% 100% 0%;
  }

  &__card-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    margin-bottom: 16px;
  }

  &__card-footer {
    display: flex;
    align-items: flex-end;
    width: 100%;
    justify-content: space-between;
  }

  &__card-content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__card-title {
    display: block;
    font-family: 'b';
    font-size: 20px;
    line-height: 120%;
    color: $menu-black;
    margin-bottom: 6px;
  }

  &__card-desc {
    display: block;
    font-family: 'r';
    font-size: 18px;
    line-height: 120%;
    color: $card-gray;
    @media (max-width: 480px) {
      font-size: 16px;
    }
  }

  &__card-btn {
    padding: 10px 20px;
    border: 1px solid $contacts_bg;
    border-radius: 10px;
    color: $contacts_bg;
    background-color: transparent;

    font-family: 'b';
    font-size: 16px;
    line-height: 110%;
    letter-spacing: 0%;
  }
}
