.guides {
  margin-bottom: 90px;
  min-height: 1px;
  @media (max-width: 575.9px) {
    margin-bottom: 23px;
  }
  &__text {
    text-align: center;
    margin-bottom: 26px;
  }

  &__head {
    display: block;
    font-size: 26px;
    font-family: 'b';
    line-height: 120%;
    color: $contacts_bg;
    margin-bottom: 4px;
    @media (max-width: 575.9px) {
      font-size: 18px;
    }
  }

  &__title {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    @media (max-width: 390px) {
      padding: 0px 5px;
    }
  }

  &__title-heading {
    font-size: 52px;
    font-family: 'ib';
    line-height: 110%;
    letter-spacing: -0.64px;
    color: $menu-black;
    margin-bottom: 14px;
    @media (max-width: 575.9px) {
      font-size: 30px;
      letter-spacing: -0.56px;
    }
    @media (max-width: 390px) {
      font-size: 25px;
    }
  }

  &__title-desc {
    color: $menu-black;
    font-size: 18px;
    font-family: 'b';
    line-height: 120%;
    @media (max-width: 575.9px) {
      color: $menu-black;
      font-size: 14px;
      font-family: 'r';
      line-height: 120%;
    }
  }

  &__grid {
    display: grid;
    gap: 26px;
    grid-template-columns: repeat(4, minmax(170px, 1fr));
    @media (max-width: 767.9px) {
      grid-template-columns: repeat(2, minmax(170px, 1fr));
    }
    @media (max-width: 575.9px) {
      gap: 18px;
      padding: 0 16px;
    }
    @media (max-width: 380px) {
      grid-template-columns: repeat(1, minmax(170px, 1fr));
    }
  }

  &__card {
    box-sizing: border-box;
    &:nth-child(8) {
      @media (max-width: 575.9px) {
        order: -3;
      }
    }
    &:nth-child(5) {
      @media (max-width: 575.9px) {
        order: -2;
      }
    }
    &:nth-child(6) {
      @media (max-width: 575.9px) {
        order: -1;
      }
    }
    @media (hover: hover) {
      &:hover {
        .guides__card-content::before {
          background: rgba(0, 0, 0, 0.8);
        }
        outline: 3px solid $grid-border;
        border-radius: 8%;
      }
    }
  }

  &__card-content {
    display: grid;
    border-radius: 8%;
    overflow: hidden;
    text-align: center;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.5);
      z-index: 2;
      border-radius: 8%;
    }
  }

  &__card-link {
    grid-area: 1 / 1;
    z-index: 5;
    align-self: center;
    justify-self: center;
    color: $white;
    width: 80%;
    display: block;
    margin: 0 auto;
    font-size: 26px;
    font-family: 'b';
    @media (max-width: 1199.9px) {
      font-size: 20px;
    }
    @media (max-width: 991.9px) {
      font-size: 18px;
    }
    @media (max-width: 767.9px) {
      font-size: 18px;
      bottom: 0;
      text-align: center;
      padding: 0 0 12px 12px;
    }
    @media (max-width: 575.9px) {
      align-self: end;
      line-height: 18px;
      letter-spacing: -0.41px;
      width: 100%;
      margin: 0;
      font-size: 18px;
      font-family: 's';
      padding: 0 0 12px 12px;
      text-align: start;
    }
    @media (max-width: 480px) {
      font-size: 16px;
    }
    @media (max-width: 380px) {
      font-size: 20px;
    }
    &:focus {
      outline: 1px solid transparent;
    }
  }

  &__card-title {
    display: block;
    @media (max-width: 575.9px) {
      width: 70%;
      line-height: 130%;
    }
  }

  &__card-img {
    grid-area: 1 / 1;
    z-index: 1;
    align-self: center;
    justify-self: center;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
