.specialists {
  width: 100%;
  overflow: hidden;
  position: relative;
  min-height: 699px;
  margin-bottom: 90px;
  @media (max-width: 575.9px) {
    margin-bottom: 40px;
  }

  &__bg {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    object-fit: cover;
    min-height: 699px;

    @media (max-width: 991.9px) {
      min-height: 824px;
    }
  }
  &__container {
    position: relative;
  }

  &__content {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    padding: 72px 0px 60px 30px;
    @media (max-width: 767.9px) {
      padding: 72px 30px 40px 30px;
      align-items: center;
    }
  }
  &__table {
    display: block;
    width: 100%;
    min-height: 190px;
    border-radius: 10px;
    background-color: rgba(27, 119, 134, 0.5);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    @media (min-width: 767.9px) {
      display: none;
    }
  }

  &__table-img {
    width: 150px;
    height: 150px;
    object-fit: cover;
    margin-top: -75px;
  }

  &__header {
    @media (max-width: 767.9px) {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 72px;
    }
  }

  &__text {
    display: block;
    font-family: 'b';
    font-size: 26px;
    line-height: 120%;
    color: $white;
    letter-spacing: -0.64px;
    @media (max-width: 767.9px) {
      margin-bottom: 6px;
    }
  }

  &__title {
    font-family: 'b';
    font-size: 52px;
    line-height: 100%;
    color: $white;
    @media (max-width: 767.9px) {
      margin-top: 4px;
      margin-bottom: 6px;
      font-size: 26px;
    }
  }

  &__label {
    display: block;
    font-family: 'r';
    font-size: 16px;
    line-height: 130%;
    color: $white;
    margin-bottom: 13.88px;
    @media (max-width: 767.9px) {
      margin-bottom: 6px;
      font-size: 14px;
    }
  }

  &__rating {
    display: inline-block;
    margin-bottom: 21px;
    & label {
      font-size: 20px;
      color: $white;
      cursor: pointer;
      @media (max-width: 767.9px) {
        font-size: 24px;
      }
    }
    & label:before {
      content: '\2605';
    }

    & input:checked ~ label:before {
      color: #ffc107;
    }

    & label:hover:before {
      color: #ccc;
    }
  }

  &__rating-star {
    display: none;
  }
  &__rating-count {
    font-size: 16px;
    line-height: 16px;
    letter-spacing: -0.41px;
    color: $white;
    font-family: 'l';
    @media (max-width: 767.9px) {
      font-size: 18px;
    }
  }

  &__description {
    width: 34.3%;
    font-family: 'r';
    font-size: 18px;
    color: $white;
    margin-bottom: 40px;
    @media (max-width: 991.9px) {
      width: 50%;
    }
    @media (max-width: 767.9px) {
      width: 80%;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
      margin-bottom: 30px;
      text-align: center;
    }
  }

  &__cards {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-bottom: 40px;
    flex-wrap: wrap;
    @media (max-width: 767.9px) {
      justify-content: center;
    }
  }

  &__card {
    background-color: white;
    border-radius: 20px;
    padding: 24px 26px;
    display: flex;
    align-items: center;
    gap: 20px;
    @media (max-width: 575.9px) {
      gap: 0;
      width: 80%;
    }
  }

  &__card-text {
    width: 280px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__card-info {
    font-family: 'r';
    font-size: 18px;
    line-height: 120%;
    color: $menu-black;
    margin-bottom: 6px;
    @media (max-width: 767.9px) {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 1;
      overflow: hidden;
    }
  }

  &__card-price {
    font-family: 'b';
    font-size: 26px;
    line-height: 120%;
    color: $contacts_bg;
  }

  &__card-btn {
    background: transparent;
  }

  &__card-img {
    width: 42px;
    height: 42px;
    object-fit: cover;
    @media (max-width: 991.9px) {
      display: none;
    }
    @media (max-width: 767.9px) {
      display: block;
    }
  }

  &__btn {
    padding: 14px 20px;
    background-color: $white;
    color: $menu-black;
    font-size: 16px;
    font-family: 'b';
    line-height: 110%;
    border-radius: 10px;
    &:focus {
      outline: 1px solid transparent;
    }
    @media (max-width: 767.9px) {
      padding: 14px 52px;
    }
  }
}
